import * as React from 'react';
import Paper from '@mui/material/Paper';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import ListItemText from '@mui/material/ListItemText';
import Avatar from '@mui/material/Avatar';
import { useEffect } from 'react';
import "./MessageBox.css"
import { useContext } from 'react';
import userContext from '../contexts/users/UserContext';
  

export default function BottomAppBar() {
  const usercntxt=useContext(userContext);

  const { onlineUsers,selected,setSelected,setLoading,users,getAllUsers,setuser,getMessages}=usercntxt;
// const scktcntxt= useContext(socketContext);
// const {onlineUsers}=scktcntxt;

  useEffect(() => {
    getAllUsers();
  },[users])
  
  useEffect(() => {
    users.forEach(element => {
      if(element._id===selected)
      {
        setuser(element);
      }
    });
    if(selected){
      getMessages(selected,localStorage.getItem("token"));
    }
    // console.log(messa)
    setLoading(true);
  }, [selected])
  
 
  return (
    <div className="msgBx">
      <React.Fragment>
        {/* <CssBaseline /> */}
        <Paper square sx={{ pb: '50px' }}>
          {/* <Typography variant="h5" gutterBottom component="div" sx={{ p: 2, pb: 0 }}> */}
            <h3 className='pb-2 p-2 mx-4 gutterBottom'>Inbox</h3>
          {/* </Typography> */}
          <List sx={{ mb: 2 }}>
            {users && users.map(({ _id, name, email, picture }) => (
              <div key={_id} className='entity-row' style={{backgroundColor:`${selected===_id?'rgb(238,174,202)':'white'}`}} onClick={()=>{
                setSelected(_id);
                // console.log(selected);
              }} >
                <React.Fragment key={_id}>
                  <ListItemButton>
                    <ListItemAvatar>
                     { onlineUsers.includes(_id) && <span className="badge rounded-pill badge-notification bg-danger">o</span>}
                      <Avatar alt="Profile Picture" src={picture} />
                    </ListItemAvatar>
                    <ListItemText className='nameAndEmail' primary={name} secondary={email} />
                  </ListItemButton>
                </React.Fragment>
              </div>
            ))}
          </List>
        </Paper>
      </React.Fragment>
    </div>
  );
}
